<template>
	<section>
    <div v-if="view =='list'">
      <!---->
      <el-col :span="24" class="toolbar" style="padding-bottom: 0px;">
        <el-form :inline="true" :model="queryForm">
          <el-form-item>
            <el-input v-model="queryForm.name" placeholder=""></el-input>
          </el-form-item>
          <el-form-item>
            <el-cascader placeholder=""
              :options="locations"
              :props="defaultProps"
              change-on-select
              @change="handleChange"
              v-model="area"
              filterable
              clearable
              style="display: block"
            ></el-cascader>
          </el-form-item>
          <el-form-item>
            <el-input v-model="queryForm.address" placeholder=""></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary" v-on:click="getTableData"></el-button>
            <el-button type="primary" @click="handleAdd" v-hasPermission="'shop.add'"></el-button>
          </el-form-item>
        </el-form>
      </el-col>

      <!---->
      <el-table :data="tableData" stripe highlight-current-row v-loading="listLoading" style="width: 100%;" :empty-text="message">
        <el-table-column prop="name" label="" width="250" show-overflow-tooltip>
        </el-table-column>
        <el-table-column prop="longitude" label="" width="200">
          <template slot-scope="scope">{{scope.row.latitude}},{{scope.row.longitude}}</template>
        </el-table-column>
        <el-table-column prop="radius" label="()" width="150">
        </el-table-column>
        <el-table-column label="">
          <template slot-scope="scope">
            <el-button size="small" @click="handleDetail(scope.$index, scope.row)" v-hasPermission="'shop.detail'"></el-button>
            <el-button size="small" type="primary" @click="handleAcode(scope.$index, scope.row)" v-hasPermission="'shop.acode'"></el-button>
            <el-button size="small" type="warning" @click="handleUpdate(scope.$index, scope.row)" v-hasPermission="'shop.update'"></el-button>
            <el-button size="small" type="danger" @click="handleDelete(scope.$index, scope.row)" v-hasPermission="'shop.delete'"></el-button>
          </template>
        </el-table-column>
      </el-table>

      <!---->
      <el-col :span="24" class="toolbar">
        <el-pagination layout="total, prev, pager, next" @current-change="handleCurrentChange" :page-size="pageSize" :total="total" prev-text="" next-text="" background style="float:right;">
        </el-pagination>
      </el-col>
    </div>

    <div v-if="view =='add' || view =='update'">
      <el-row>
        <el-col :span="16">
          <el-form :model="shopForm" label-width="100px" :rules="shopFormRules" ref="shopForm">
            <el-form-item label="" prop="name">
              <el-input v-model="shopForm.name" auto-complete="off" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="" prop="code">
              <el-input v-model="shopForm.code" auto-complete="off" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="" prop="locationId">
              <el-cascader placeholder=""
                :options="locations"
                :props="defaultProps"
                change-on-select
                @change="handleLocationChange"
                v-model="location"
                filterable
                clearable
                style="display: block"
              ></el-cascader>
            </el-form-item>
            <el-form-item label="" prop="address">
              <el-input v-model="shopForm.address" auto-complete="off" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="" prop="ll" placeholder=",">
              <el-input v-model="shopForm.ll" auto-complete="off">
                <template slot="append">
                  <a :href="lbs" target="_blank" class="lbs"></a>
                </template>
              </el-input>
            </el-form-item>
            <el-form-item label="" prop="radius">
              <el-input v-model="shopForm.radius" auto-complete="off" placeholder="">
                <template slot="append"></template>
              </el-input>
            </el-form-item>
          </el-form>
          <div class="btns">
            <el-button type="primary" size="small" @click="submitShop"></el-button>
            <el-button size="small" @click="back"></el-button>
          </div>
        </el-col>
      </el-row>
    </div>

    <div v-if="view =='detail'">
      <div class="btns">
        <el-button size="small" @click="back"></el-button>
      </div>
    </div>

    <div v-if="view =='acode'">
      <el-row>
        <el-col :span="16">
          <el-form :model="acodeForm" label-width="100px" :rules="acodeFormRules" ref="acodeForm">
            <el-form-item label="" prop="points">
              <el-input v-model="acodeForm.points" auto-complete="off" placeholder=""></el-input>
            </el-form-item>
            <el-form-item label="">
              <el-input v-model="acodeForm.width" auto-complete="off" placeholder="px 280px 1280px 430px"></el-input>
            </el-form-item>
          </el-form>
          <div class="btns">
            <el-button type="primary" size="small" @click="createAcode"></el-button>
            <el-button size="small" @click="back"></el-button>
          </div>
        </el-col>
      </el-row>
    </div>

    <el-dialog :visible.sync="showAcode">
		  <img width="100%" v-lazy="acodeForm.src">
		</el-dialog>

	</section>
</template>

<script>
	export default {
		data() {
			return {
				queryForm: {
          name: '',
          address: '',
          locationId: ''
        },
        tableData: [],
        total: 0,
        pages: 0,
        page: 1,
        pageSize: 8,
        sort: 's.id',
        order: 'desc',
        message: '',
        listLoading: false,

        locations: [],
        defaultProps: {value: 'id', label: 'locationName', children: 'subLocationList'},
        area: [],
        
        view: 'list',

        shop: {},

        shopForm: {},
        shopFormRules: {
          name: [
            { required: true, message: '', trigger: 'blur' }
          ],
          code: [
            { required: true, message: '', trigger: 'blur' }
          ],
          locationId: [
            { required: true, message: '', trigger: 'change' }
          ],
          address: [
            { required: true, message: '', trigger: 'blur' }
          ],
          ll: [
            { required: true, message: '', trigger: 'blur' }
          ],
          radius: [
            { required: true, message: '', trigger: 'blur' }
          ]
        },
        location: [],
        lbs: 'https://lbs.qq.com/tool/getpoint/index.html',

        acodeForm: {},
        acodeFormRules: {
          points: [
            { required: true, message: '', trigger: 'blur' }
          ]
        },

        service: '',
        showAcode: false
			}
		},
		methods: {
			handleCurrentChange(val) {
				this.page = val;
				this.getTableData();
      },
      handleChange(value){
        if(value.length>0){
          this.queryForm.locationId = value[value.length-1]
        }else{
          this.queryForm.locationId = ''
        }
      },
      handleLocationChange(value){
        if(value.length>0){
          this.shopForm.locationId = value[value.length-1]
        }else{
          this.shopForm.locationId = ''
        }
      },
      getLocationData() {
        this.$ajax.get('/system/location/tree').then((res)=>{
          if(res.success){
            res.root.subLocationList.forEach(element => {
              this.locations.push(element)
            });
          }
        })
      },
			//
			getTableData() {
				let para = {
          name: this.queryForm.name,
          address: this.queryForm.address,
          locationId: this.queryForm.locationId,
          page: this.page,
          pageSize: this.pageSize,
          sort: this.sort,
          order: this.order
				};
        this.listLoading = true
				this.$ajax.get('/shop/page',para).then((res) => {
          this.listLoading = false
          if(res.success){
            this.page = res.page
            this.pageSize = res.pageSize
            this.total = res.total
            this.pages = res.pages
            this.tableData = res.data
          }else{
            this.tableData = []
            this.page = 0
            this.total = 0
            this.pages = 0
            this.message = res.message
          }
				})
			},
			//
			handleDelete: function (index, row) {
				this.$confirm(''+row.name+'?', '', {
					type: 'warning'
				}).then(() => {
					this.listLoading = true;
					this.$ajax.post('/shop/delete/'+row.id).then((res) => {
            this.listLoading = false;
            if(res.success){
              this.$message.success('')
              this.getTableData()
            }else{
              this.$message.error(res.message)
            }
					})
				}).catch(() => {

				})
			},
			//
			handleUpdate: function (index, row) {
        this.$ajax.get('/shop/get/'+row.id).then((res)=>{
          if(res.success){
            this.shopForm = res.data
            this.shopForm.ll = this.shopForm.latitude+','+this.shopForm.longitude
            if(res.data.locationArray!=null){
              this.location = this.shopForm.locationArray
            }else{
              this.location = []
            }
            this.view = 'update'
          }else{
            this.$message.error(res.message)
          }
        })
			},
      //
      handleDetail: function(index, row){
        this.$ajax.get('/shop/get/'+row.id).then((res)=>{
          if(res.success){
            this.shop = res.data
            this.view = 'detail'
          }else{
            this.$message.error(res.message)
          }
        })
      },
      handleAcode: function(index, row){
        this.showAcode = false
        this.acodeForm = {}
        this.$ajax.get('/system/config/name', {propertyName: 'wx.acode.page'}).then((res)=>{
          if(res.success){
            this.acodeForm.page = res.data
            this.acodeForm.id = row.id
            this.view = 'acode'
          }else{
            this.$message.error('page')
          }
        })
      },
      back: function(){
        this.view = 'list'
      },
      handleAdd: function(){
        this.shopForm = {}
        this.view = 'add'
      },
      submitShop: function(){
        this.$refs.shopForm.validate((valid) => {
          if (valid) {
            let split = this.shopForm.ll.split(',')
            if(split.length!=2){
              this.$message.error('')
              return
            }
            this.shopForm.latitude = split[0]
            this.shopForm.longitude = split[1]
            this.$confirm('', '', {}).then(() => {
              this.$ajax.post('/shop/'+(this.shopForm.id?'update':'add'),this.shopForm).then((res) => {
                if(res.success){
                  this.$message.success('')
                  this.getTableData()
                  this.view = 'list'
                }else{
                  this.$message.error(res.message)
                }
              })
            })
          }
        })
      },
      createAcode: function(){
        this.$refs.acodeForm.validate((valid) => {
          if (valid) {
            this.acodeForm.scene = this.acodeForm.id + ',' + this.acodeForm.points
            let acodeUrl = this.action+'?page='+this.acodeForm.page+'&scene='+this.acodeForm.scene
            if(this.acodeForm.width){
              acodeUrl = acodeUrl + '&width='+this.acodeForm.width
            }
            this.acodeForm.src = acodeUrl
            this.showAcode = true
          }
        })
      }
    },
    mounted(){
      this.service = this.$service
    },
    created(){
      this.getLocationData()
      this.getTableData()
    },
    computed: {
      action: function(){
        return this.service + "/wx/acode"
      }
    }
	}

</script>

<style scoped lang="scss">
.btns{
  clear: both;
  width: 100%;
  text-align: center;
}
.lbs{
  display: block;
  color: #606266;
  line-height: 1;
  text-decoration: none;
  font-size: 12px;
  font-family: Arial;
  white-space: nowrap;
}
</style>



